<template>
  <scroll ref="suggest"
          class="suggest"
          :data="result"
          :pullup="pullup"
          :beforeScroll="beforeScroll"
          @scrollToEnd="searchMore"
          @beforeScroll="listScroll"
  >
    <ul class="suggest-list">
      <li @click="selectItem(item)" class="suggest-item" v-for="item in result">
        <div class="icon">
          <i :class="getIconCls(item)"></i>
        </div>
        <div class="name">
          <p class="text" v-html="getDisplayName(item)"></p>
        </div>
      </li>
      <loading v-show="hasMore" title=""></loading>
    </ul>
    <div v-show="!hasMore && !result.length" class="no-result-wrapper">
      <no-result title=""></no-result>
    </div>
  </scroll>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import Loading from 'base/loading/loading'
  import NoResult from 'base/no-result/no-result'
  import {search} from 'api/search'
  import {ERR_OK} from 'api/config'
  import {createSong} from 'common/js/song'
  import {mapMutations, mapActions} from 'vuex'
  import Singer from 'common/js/singer'
  import {getMusic} from "api/getMusic";


  const TYPE_SINGER = 'singer'
  const perpage = 20

  export default {
    props: {
      // zhida,
      showSinger: {
        type: Boolean,
        default: true
      },
      // 
      query: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        // 
        page: 1,
        // scroll
        pullup: true,
        beforeScroll: true,
        // 
        hasMore: true,
        // 
        result: [],
        // 
        singerFLag:true
      }
    },
    methods: {
      // query
      refresh() {
        this.$refs.suggest.refresh()
      },
      // 
      search() {
        this.page = 1
        this.hasMore = true
        // scroll
        this.$refs.suggest.scrollTo(0, 0)
        if(this.query === ""){
          this.singerFLag = true
        }
        search(this.query, this.page, this.showSinger, perpage).then((res) => {
          if (res.code === ERR_OK) {
            // console.log("1",res.data)
            this.result = this._genResult(res.data)
            // console.log("34",this.result)
            this._checkMore(res.data)
          }
        })
      },
      // @scrolltoEnd
      searchMore() {
        if (!this.hasMore) {
          return
        }
        // ++
        this.page++
        search(this.query, this.page, this.showSinger, perpage).then((res) => {
          if (res.code === ERR_OK) {
            // 
            let temp = this.result
            this.result = this._genResult(res.data)
            // console.log("temp",temp)
            this.result.splice(0,0,...temp)
            // console.log("more",this.result)
            this._checkMore(res.data)
          }
          // _genResult,_normalizeSongsgetMusic
          // concat
          //  this.result
          // if (res.code === ERR_OK) {
          //   this.result = this.result.concat(this._genResult(res.data))
          //   this._checkMore(res.data)
          // }
        })
      },
      // @beforeScrollinput
      listScroll() {
        this.$emit('listScroll')
      },
      selectItem(item) {
        // ,
        if (item.type === TYPE_SINGER) {
          const singer = new Singer({
            id: item.singermid,
            name: item.singername
          })
          this.$router.push({
            path: `/search/${singer.id}`
          })
          this.setSinger(singer)
        } else {
          // 
          this.insertSong(item)
        }
        // search.vuesaveSearch => actionssaveSearchHistory => cachelocalstorage
        this.$emit('select', item)
      },
      // lititle+
      getDisplayName(item) {
        if (item.type === TYPE_SINGER) {
          return item.singername
        } else {
          return `${item.name}-${item.singer}`
        }
      },
      // 
      getIconCls(item) {
        if (item.type === TYPE_SINGER) {
          return 'icon-mine'
        } else {
          return 'icon-music'
        }
      },
      // 

      _genResult(data) {
        //
        // ,
        let ret =[]
        if (data.song) {
          ret = this._normalizeSongs(data.song.list)
        }
        if (data.zhida && data.zhida.singermid && this.singerFLag) {
          ret.splice(0,0,{...data.zhida, ...{type: TYPE_SINGER}})
          this.singerFLag=false
        }
        console.log(".1.",ret)
        // this._fixResult(data,ret)
        // console.log(".2.",ret)
        return ret
      },
      _normalizeSongs(list) {
        let ret = []
        list.forEach((musicData) => {
          if (musicData.songid && musicData.albummid) {
            getMusic(musicData.songmid).then((res)=>{
              if(res.code === ERR_OK){
                let svkey=res.data.items
                let songVkey= svkey[0].vkey
                let newSong =createSong(musicData,songVkey)
                // console.log(newSong)
                ret.push(newSong)
                // console.log("6",ret)
              }
            })
          }
        })
        return ret
      },

      _checkMore(data) {
        const song = data.song
        // 
        if (!song.list.length || (song.curnum + song.curpage * perpage) > song.totalnum) {
          this.hasMore = false
        }
      },
      ...mapMutations({
        setSinger: 'SET_SINGER'
      }),
      ...mapActions([
        'insertSong'
      ])
    },
    watch: {
      // query
      query(newQuery) {
        this.search(newQuery)
      }
    },
    components: {
      Scroll,
      Loading,
      NoResult
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 0 30px
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: $color-text-d
      .name
        flex: 1
        font-size: $font-size-medium
        color: $color-text-d
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



